import { authService, dbService, storageService } from "myBase";
import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import { v4 as uuidv4 } from "uuid";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes, faEdit } from "@fortawesome/free-solid-svg-icons";

export default ({ refreshUser, userObj }) => {
  const history = useHistory();
  const [newDisplayName, setNewDisplayName] = useState(userObj.displayName);
  const [attachment, setAttachment] = useState("");
  const [newPhotoUrl, setNewPhotoUrl] = useState(userObj.photoUrl);
  const photoUrl = userObj.photoUrl;

  const onLogOutClick = () => {
    authService.signOut();
    history.push("/");
  };
  const onChange = (event) => {
    const {
      target: { value },
    } = event;
    setNewDisplayName(value);
  };
  const onSubmit = async (event) => {
    event.preventDefault();
    if (userObj.displayName !== newDisplayName) {
      await userObj.updateProfile({
        displayName: newDisplayName,
      });
      refreshUser();
    }
    if (userObj.photoUrl !== newPhotoUrl) {
      let attachmentUrl = "";
      if (attachment !== "") {
        const attachmentRef = storageService
          .ref()
          .child(`${userObj.uid}/${uuidv4()}`);
        const response = await attachmentRef.putString(attachment, "data_url");
        attachmentUrl = await response.ref.getDownloadURL();
      }
      setAttachment("");
      await userObj.updateProfile({
        photoURL: attachmentUrl,
      });
      await storageService.refFromURL(photoUrl).delete();
      refreshUser();
    }
  };
  const onFileChange = async (event) => {
    const {
      target: { files },
    } = event;
    const theFile = files[0];
    const reader = new FileReader();
    reader.onloadend = (finishedEvent) => {
      const {
        currentTarget: { result },
      } = finishedEvent;
      setAttachment(result);
      setNewPhotoUrl(result);
    };
    reader.readAsDataURL(theFile);

  }

  return (
    <div className="container">
      <form onSubmit={onSubmit} className="profileForm">
        <div style={{ textAlign: 'center' }}>
          <img id="profile" htmlFor="attach-file"
            src={newPhotoUrl}
            width="150px" height="150px" style={{ borderRadius: 50 + '%' }} />
          <div style={{ marginTop: 10 + 'px' }}>
            <label htmlFor="attach-file" className="factoryInput__label">
              <span>
                Edit Photo
                <FontAwesomeIcon icon={faEdit} />
              </span>
            </label>
            <input
              id="attach-file"
              type="file"
              accept="image/*"
              onChange={onFileChange}
              style={{
                opacity: 0,
              }}
            />
          </div>
        </div>
        <input
          onChange={onChange}
          type="text"
          autoFocus
          placeholder="Display name"
          value={newDisplayName}
          className="formInput"
        />
        <input
          type="submit"
          value="Update Profile"
          className="formBtn"
          style={{
            marginTop: 10,
          }}
        />
      </form>
      <span className="formBtn cancelBtn logOut" onClick={onLogOutClick}>
        Log Out
      </span>
    </div>
  );
};